import popMe from "@/components/popupMe.vue";
export default {
	components:{
		popMe
	},
	data() {
		return {
			secondPassword:'',
			userDetail: '',
			index:''
		};
	},
	onShow:function(){
		this.fetch()
	},
	onLoad:function(){
		this.fetch()
	},
	methods:{
		toAccountManage:function(){
			// uni.showToast({
			// 	title: '待開放',
			// 	icon:'none',
			// 	duration: 1000
			// });
			uni.navigateTo({
				url: './AccountManager',
				
			});
		},
		toSetIcon:function(){
			uni.navigateTo({
				url: './setIcon',	
			});
		},
		showDialog:function(e){
			this.index = e
			this.$refs.dialog.show()
		},
		toShouKuan:function(){
			uni.navigateTo({
				url: './payMode'
			
			});
		},
		about:function(){
			uni.navigateTo({
				url: './about',
				
			});
		},
		async fetch(){
			const res  = await this.$http.get('/user/details')
			console.log(res)
			this.userDetail = res.data.data
			this.$store.dispatch('copeFun',this.userDetail.userCode)
			console.log(this.$store.state.userCode)
		},
		toIdentity:function(){
			this.$refs.dialog.show()
			// uni.navigateTo({
			// 	url: './identityManage',
				
			// });
		},
			
		toMyTeam:function(){
			uni.navigateTo({
				url: '/pages/userCenter/myTeam'
			});
		},
		toAddress:function(){
			uni.navigateTo({
				url: './addressBook',
				
			});
		},
		toSecureCenter:function(){
			uni.navigateTo({
				url: './secureCenter/secureCenter',
				
			});
		},
		toShare:function(){
			
			if (this.userDetail.staticUserStatus < 1){
				uni.navigateTo({
					url: `./share`,
					
				});
			}
			else {
				uni.navigateTo({
					url: `./share?userCode=${this.userDetail.userCode}&creditToken=${this.userDetail.creditToken}`,
					
				});
				
			}
			
		},
		toSubAccounter:function(){
			// uni.showToast({
			// 	title: '待開放',
			// 	icon:'none',
			// 	duration: 1000
			// });
			uni.navigateTo({
				url: './subAccounter'
			});
		},
		toManage:function(){
			this.$refs.dialog.show()
		},
		confirmInput:function(e){
			this.secondPassword = e
			this.$refs.dialog.content = ''
			
			this.checkPayPassword()
			
			
		},
		async checkPayPassword(){
			let data ={
				payPassword: this.secondPassword
			}
			const res = await this.$http.get('/user/check_pay_pass',{data: data})
			console.log(res)
			this.secondPassword = ''
			if (res.data.data.msg=='true'){
				if (this.index ==1){
					uni.navigateTo({
						url: './identityManage',
										
					});
				}else if (this.index ==2){
					this.toMyTeam()
				}
				
			}else {
				uni.showToast({
					title: '支付密码不正确',
					icon:'none',
					duration: 1000
				});
			}
		},
		toSetting:function(){
			uni.navigateTo({
				url: './setting',
			});
		}
	}
}
